import React, { ReactElement} from 'react';
import { Route, Redirect, Switch } from 'react-router-dom';
import Pairings, { PairingsProps } from './Pairings';
import NotFound from '../404';

export default function Round(props: PairingsProps): ReactElement {

	return (
		<Route render={({location}) => {
			return (
				<Switch location={location}>
					<Route exact path='/:id/pairings' render={(rProps) =>  {console.log(rProps); return <Redirect to={rProps.match.url + (rProps.match.url.endsWith('/') ? '' : '/') + props.status.round}/>}} />
					<Route path='/:id/pairings/:round' render={(rProps) => {
						let round = parseInt(rProps.match.params.round);
						if (isNaN(round)) return <NotFound />;
						if (round < 0 || round > props.settings.totalRounds) return <NotFound />;
						return <Pairings
							round={round}
							{...props}
						/>
					}} />
					<Route path='/:id/pairings/*' component={NotFound} status={404} />
				</Switch>
			);
		}}/>
	);

}